import test from 'ava';
import add from './add';

test('Empty string', t => {
  t.is(add(''), 0);
});

test('One number', t => {
  t.is(add('5'), 5);
});

test('Two numbers', t => {
  t.is(add('5,3'), 8);
});

test('Unknown amount of numbers', t => {
  t.is(add('2,4,7,10'), 23);
});

test('Unknown amount of numbers width delimeter \n', t => {
  t.is(add('2,4,7,10'), 24);
});
